* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  background-color: #091309;
}

section {
  display: flex;
  justify-content: center;
}

#msg {
  border: 1px solid darkolivegreen;
  width: 30vw;
  height: 10vh;
  background-color: lightslategray;
  overflow-y: scroll;
}

#msg .span {
  font-size: 1.6vw;
  word-wrap: break-word;
}

#chat-input {
  width: 30vw;
}

.singleMessage {
  display: flex;
  justify-content: space-between;
  width: 100%;
  min-height: 3.5vh;
  overflow: auto;
}

.score {
  display: flex;
  flex-flow: column;
  width: 5vw;
  height: 15vh;
  align-items: flex-end;
  text-align: right;
  color: white;
  position: relative;
  margin-left: 27vw;
}

.score h3 {
  display: flex;
}

.score h3 .oScore {
  color: yellow;
  margin-right: 10px;
}

.score h3 .xScore {
  color: yellow;
}

@keyframes incomer {
  0% {
    transform: scale(0.8);
  }
  50% {
    transform: scale(1.2);
  }
  75% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}

.incomin {
  animation: 1s forwards incomer;
}

@media only screen and (max-width: 800px) {
  section {
    flex-flow: column;
  }
  .score {
    width: 100vw;
    display: flex;
    flex-flow: row;
    justify-content: space-between;
  }
  .score h3 {
    justify-content: space-between;
  }
}

@keyframes roattion {
  99% {
    transform: rotate(360deg);
    scale: 1.4;
  }
  100% {
    transform: scale(1);
  }
}

@keyframes courtainFall {
  100% {
    height: 100vh;
    opacity: 1;
  }
}

.winner {
  animation: 0.5s forwards courtainFall;
}

.winner__alert {
  position: absolute;
  width: 99vh;
  height: 0vh;
  background-color: #395239;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  background: rgba(255, 255, 255, 0.5);
  font-family: Arial, Helvetica, sans-serif;
  font-size: 3rem;
  font-size: 2rem;
  font-weight: 600;
  color: #464433;
  opacity: 0;
}

spam {
  font-size: 3rem;
  font-weight: 900;
  color: red;
}

.ttt5 {
  background-color: #091309;
  min-width: 100vh;
  min-height: 100vh;
  margin: auto;
  display: flex;
  justify-content: center;
  align-content: center;
}

.board {
  width: 99vh;
  height: 99vh;
  background-color: #395239;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: center;
}

.square {
  width: 4.9vh;
  height: 4.9vh;
  border: 1px solid black;
  background-color: #ebebcf;
  border-radius: .5vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.square:hover {
  scale: 1.1;
}

.rotator {
  animation: 0.4s forwards roattion;
}
